<template>
  <el-form ref="form" :model="formData" :inline="true">
    <el-form-item label="报关状态:">
      <el-select v-model="formData.status" placeholder="请选择" clearable>
        <el-option
          v-for="item in orderStatus"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item class="mg-b-20">
      <el-button type="primary" @click="search(formData)">搜索</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  data() {
    return {
      deliveryWay: this.$store.state.deliveryWay,
      unDeliver: false,
      noDeliver: true,
      orderStatus: [
        { value: 1, label: "洽谈中" },
        { value: 2, label: "已完成" }
      ],
      datePicker: []
    };
  },
  computed: {
    isSystem() {
      return this.$store.state.isSystem;
    }
  },
  props: {
    formData: {
      type: Object,
      default() {
        return {
          status: "", // 状态
          sysTag: "",
          pageNum: 1
        };
      }
    },
    search: {
      type: Function,
      default() {
        return () => {};
      }
    }
  },
  methods: {
    // search() {
    //   this.$emit("order-search", this.formData);
    // }
  }
};
</script>



