<template>
  <div v-title="'收付汇详情'">
    <div class="oil-title-1">企业收汇信息</div>
    <div class="payment mg-t-20">
      <div class="oil-card">
        <div class="oil-card-info oil-row">
          <el-row :gutter="10">
            <el-col :span="12">
              <span class="oil-bold font-16">最终结算单价(美元/桶)：</span>
              <span class="font-24">{{ pageData.settle_unit_price }}</span>
            </el-col>
            <el-col :span="12">
              <span class="oil-bold font-16">最终结算总金额(美元)：</span>
              <span class="font-24">{{ pageData.settle_ttl_amt }}</span>
            </el-col>
          </el-row>
        </div>
        <div class="oil-card-box oil-row">
          <el-row :gutter="10">
            <el-col :span="12">
              <el-row>
                <el-col :span="6">合同号：</el-col>
                <el-col :span="18">{{ pageData.purchase_contract_no }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="6">订单类型：</el-col>
                <el-col :span="18">{{ pageData.order_type }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="6">币种：</el-col>
                <el-col :span="18">{{ pageData.currency }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="6">金额：</el-col>
                <el-col :span="18">{{ pageData.order_amt }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="6">提单日：</el-col>
                <el-col :span="18">{{ pageData.bill_date }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="6">付款日：</el-col>
                <el-col :span="18">{{ pageData.payment_date }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="6">油种：</el-col>
                <el-col :span="18">{{ pageData.oil_type }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="6">最终用户：</el-col>
                <el-col :span="18">{{ pageData.final_user }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="6">供应商名称：</el-col>
                <el-col :span="18">{{ pageData.supplier }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="6">合同量(桶)：</el-col>
                <el-col :span="18">{{ pageData.contract_num }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="6">溢短装：</el-col>
                <el-col :span="18">{{ pageData.tolerance }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="6">贸易条款：</el-col>
                <el-col :span="18">{{ pageData.trade_terms }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="6">计价基准：</el-col>
                <el-col :span="18">{{ pageData.price_standard }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="6">付款期/付款条件：</el-col>
                <el-col :span="18">{{ pageData.payment_term }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="6">计价期：</el-col>
                <el-col :span="18">{{ pageData.price_term }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="6">船名：</el-col>
                <el-col :span="18">{{ pageData.ship_name }}</el-col>
              </el-row>
            </el-col>
            <el-col>
              <el-row>
                <el-col :span="3">计价公式：</el-col>
                <el-col :span="21">{{ pageData.price_formula }}</el-col>
              </el-row>
            </el-col>
          </el-row>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import API from "@/api/api";
import { fmtPageData } from "@/util/index";
import { mapGetters } from "vuex";

export default {
  data() {
    return {
      pageData: {
        settle_unit_price: "",
        settle_ttl_amt: null,
        deal_no: null,
        order_type: null,
        currency: null,
        order_amt: "",
        bill_date: "",
        payment_date: "",
        oil_type: "",
        final_user: "",
        supplier: "",
        contract_num: null,
        tolerance: null,
        trade_terms: "",
        price_standard: null,
        payment_term: null,
        price_term: "",
        ship_name: null,
        price_formula: null
      }
    };
  },
  computed: {
    ...mapGetters(["deal_no", "blockData"]),
    isView() {
      return this.$route.query.isView;
    }
  },
  methods: {
    reqPageData() {
      let oid = {
        deal_no: this.deal_no
      };
      this.$http(API.PAYMENT, oid, "get").then(res => {
        let odata = res.data;
        if (odata) {
          this.pageData = fmtPageData(odata);
        }
      });
    }
  },
  mounted() {
    // if (this.isView) {
    //   this.pageData = this.blockData;
    // } else {
      this.reqPageData();
    // }
  }
};
</script>



