<template>
  <div class="blank-box">
    <ul class="inline">
      <li>
        <div class="oil-portrait">
          <img src="../assets/logo_zhsy.png" alt="" srcset="">
        </div>
      </li>
      <li class="mg-l-30">
        <div class="font-20">{{ pageData.enterpriseName }}</div>
        <div>{{ pageData.enterpriseNameEn }}</div>
      </li>
    </ul>
    <div class="mg--15 oil-card-info oil-row">
      <span>装货时间事实记录</span>
      <span>LAYTIME STATEMENT OF FACTS</span>
    </div>
    <div>
      <el-row :gutter="20">
        <el-col :span="2">
          <div>船名</div>
          <div>M/V</div>
        </el-col>
        <el-col :span="8">
          <div class="oil-underline">{{ pageData.vessel }}</div>
        </el-col>
        <el-col :span="5">
          <div>航次</div>
          <div>VOY.NO.</div>
        </el-col>
        <el-col :span="9">
          <div class="oil-underline">{{ pageData.voyage }}</div>
        </el-col>
      </el-row>
      <el-row class="mg-t-10" :gutter="20">
        <el-col :span="2">
          <div>装货名称</div>
          <div>Loading</div>
        </el-col>
        <el-col :span="8">
          <div class="oil-underline">{{ pageData.goodsName }}</div>
        </el-col>
        <el-col :span="5">
          <div>吨数</div>
          <div>MT</div>
        </el-col>
        <el-col :span="9">
          <div class="oil-underline">{{ pageData.metricTon }}</div>
        </el-col>
      </el-row>
      <el-row class="mg-t-10" :gutter="20">
        <el-col :span="2">
          <div>日期</div>
          <div>Date</div>
        </el-col>
        <el-col :span="8">
          <div class="oil-underline">{{ pageData.sofDate }}</div>
        </el-col>
        <el-col :span="5">
          <div>港口</div>
          <div>LOADING/DISCHARGING PORT</div>
        </el-col>
        <el-col :span="9">
          <div class="oil-underline">{{ pageData.port }}</div>
        </el-col>
      </el-row>
    </div>
    <div class="oil-box-border index-table">
      <el-table :data="sofTable">
        <el-table-column label="日期" prop="detailDate"></el-table-column>
        <el-table-column label="星期" prop="week"></el-table-column>
        <el-table-column label="开始时间" prop="beginTime"></el-table-column>
        <el-table-column label="结束时间" prop="endTime"></el-table-column>
        <el-table-column label="说明" prop="instruction"></el-table-column>
      </el-table>
    </div>
    <el-row class="mg-t-20" :gutter="20">
      <el-col :span="2">
        <div>备注</div>
        <div>REMARK</div>
      </el-col>
      <el-col :span="22">
        <div>{{ pageData.remark }}</div>
      </el-col>
    </el-row>
    <div class="mg-t-20">
      <el-row :gutter="20">
        <el-col :span="2">
          <div>船长</div>
          <div>CAPTAIN</div>
        </el-col>
        <el-col :span="10">
          <div class="oil-underline"></div>
        </el-col>
        <el-col :span="2">
          <div>代理公司</div>
          <div>AS AGENT</div>
        </el-col>
        <el-col :span="10">
          <div class="oil-underline">{{ pageData.enterpriseName }}</div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import API from "@/api/api";
import { mapGetters } from "vuex";
import { fmtPageData } from "@/util/index";

export default {
  data() {
    return {
      tableData: [],
      pageData: {
        detail: [],
        serialNumber: "",
        contractNO: null,
        enterpriseName: "",
        enterpriseNameEn: "",
        vessel: "",
        voyage: "",
        goodsName: "",
        sofDate: "",
        metricTon: "",
        port: "",
        shipGrossWeight: "",
        shipNetWeight: "",
        dealno: "",
        remark: "",
        sofDetail: [
          // {
          //   id: "id",
          //   detailDate: "日期（格式：JUN 20）",
          //   week: "星期",
          //   beginTime: "开始时间",
          //   endTime: "结束时间",
          //   instruction: "说明"
          // }
        ]
      }
    };
  },
  computed: {
    ...mapGetters(["deal_no", "blockData"]),
    sofTable: {
      get(){
        return this.pageData.detail;
      },
      set(val) {
        this.pageData.detail = val;
      }
    },
    isView() {
      return this.$route.query.isView;
    }
  },
  watch: {
    blockData(val) {}
  },
  methods: {
    reqPageData() {
      let oid = { deal_no: this.deal_no };
      this.$http(API.SHIP_SOF, oid, "get").then(res => {
        let odata = res.data;
        if (odata) {
          let pdata = fmtPageData(odata);
          this.pageData = pdata.pageData;
          // console.log(this.pageData);
          let tdata = pdata.attachmentList;
          this.$store.commit("setTableData", tdata);
        }
      });
    }
  },
  mounted() {
    // if (this.isView) {
    //   this.pageData = this.blockData;
    // } else {
      this.reqPageData();
    // }
  }
};
</script>



