<template>
  <el-dialog :visible="true" :title="params.title" width="800px" @close="close">
    <!-- 其他事项确认 -->
    <div v-if="popIndex===1">
      <div style="text-align:center;">
        <div style="font-size:48px;" class="el-icon-loading"></div>
        <div class="mg-t-10">正在进行文件校验</div>
      </div>
      <div class="oil-row mg-t-20" style="background-color:#F7F7F9;">
        <div class="oil-label">链上附件的区块信息：</div>
        <div class="mg-t-10">
          <span class="oil-label">HASH值</span>
          <span>{{ txid }}</span>
        </div>
        <div class="oil-label mg-t-20">校验附件的区块信息：</div>
        <div class="mg-t-10">
          <span class="oil-label">HASH值</span>
          <span>{{ txid }}</span>
        </div>
      </div>
    </div>
    <!-- 随附单证 -->
    <div v-if="popIndex===2">
      <div style="text-align:center;">
        <div style="font-size:48px;" class="el-icon-document color-success"></div>
        <div class="mg-t-10 color-success">比对结果：所校验附件没有被篡改过</div>
      </div>
      <div class="oil-row mg-t-20" style="background-color:#F7F7F9;">
        <div class="oil-label">链上附件的区块信息：</div>
        <div class="mg-t-10">
          <span class="oil-label">HASH值</span>
          <span>{{ txid }}</span>
          <span class="el-icon-check oil-label"></span>
        </div>
        <div class="oil-label mg-t-20">校验附件的区块信息：</div>
        <div class="mg-t-10">
          <span class="oil-label">HASH值</span>
          <span class="color-success">{{ txid }}</span>
          <span class="el-icon-check color-success"></span>
        </div>
      </div>
    </div>
    <!-- 业务事项 -->
    <div v-if="popIndex===3">
      <div style="text-align:center;">
        <div style="font-size:48px;" class="el-icon-document color-danger"></div>
        <div class="mg-t-10 color-danger">比对结果：所校验附件与链上附件Hash值不同，建议核对。</div>
      </div>
      <div class="oil-row mg-t-20" style="background-color:#F7F7F9;">
        <div class="oil-label">链上附件的区块信息：</div>
        <div class="mg-t-10">
          <span class="oil-label">HASH值</span>
          <span>{{ txid }}</span>
          <span class="el-icon-check oil-label"></span>
        </div>
        <div class="oil-label mg-t-20">校验附件的区块信息：</div>
        <div class="mg-t-10">
          <span class="oil-label">HASH值</span>
          <span class="color-danger">{{ txid }}</span>
          <span class="el-icon-check color-danger"></span>
        </div>
      </div>
    </div>
    <!-- 区块链存证证书 -->
    <div v-if="popIndex===4" class="cert-box">
      <div class="cert-pad">
        <div class="cert-body">
          <div class="text-center">
            <img src="/static/images/oilbank.png" alt="" srcset="">
          </div>
          <div class="text-center">
            <span style="font-size:28px;">区块链存证证书</span>
          </div>
          <!-- <div class="text-center">
            <span>证书编号：</span>
            <span>{{ id }}</span>
          </div> -->
          <div class="mg-t-20 color-info text-center">
            <i class="el-icon-date"></i>
            <span>区块链网络通用协调时间：{{ pageData.createTime }}</span>
          </div>
          <div class="mg-t-30 text-center">
            <div class="oil-title-2">区块链存证ID:</div>
            <div class="mg-t-10">{{ txid }}</div>
          </div>
          <div class="cert-list mg-t-30"></div>
          <el-row class="mg-t-30">
            <el-col :span="4">文件摘要值：</el-col>
            <el-col :span="20">{{ pageData.fileSummary }}</el-col>
          </el-row>
          <el-row class="mg-t-10">
            <el-col :span="4">文件名称：</el-col>
            <el-col :span="20">{{ pageData.fileName }}</el-col>
          </el-row>
          <el-row class="mg-t-10">
            <el-col :span="4">文件类型：</el-col>
            <el-col :span="20">{{ pageData.fileType }}</el-col>
          </el-row>
          <el-row class="mg-t-10">
            <el-col :span="4">大小：</el-col>
            <el-col :span="20">{{ pageData.fileSize }}</el-col>
          </el-row>
          <el-row class="mg-t-10">
            <el-col :span="4">数据主体：</el-col>
            <el-col :span="20">{{ pageData.dataCopyright }}</el-col>
          </el-row>
          <el-row class="mg-t-10">
            <el-col :span="4">存证时间：</el-col>
            <el-col :span="20">{{ pageData.createTime }}</el-col>
          </el-row>
          <el-row class="mg-t-30">
            <span class="font-12 color-info">声明：本证书通过壹能链网络存证，为平台数据做出全球通行的记录声明。</span>
          </el-row>
        </div>
      </div>
    </div>
  </el-dialog>
</template>

<script>
import API from "@/api/api";

export default {
  data() {
    return {
      uploadUrl: API.FILE_UPLOAD,
      pageData: {
        eventCode: "",
        fileName: "",
        createTime: "",
        fileSize: "",
        dataCopyright: "",
        filePath: "",
        fileSummary: "",
        fileType: ""
      },
      id: ""
    };
  },
  computed: {
    popIndex: {
      get() {
        return this.params.index;
      },
      set(val) {
        this.params.index = val;
      }
    },
    txid: {
      get(){
        return this.params.data.txid;
      },
      set(val) {}
    }
  },
  props: {
    params: {
      type: Object,
      default() {
        return {
          title: "提示",
          index: 0,
          data: {}
        };
      }
    },
    close: {
      type: Function,
      default() {}
    }
  },
  methods: {
    reqChainCheck() {
      let id = this.params.data.id;
      this.$http(API.VERIFY, { id }, "get").then(res => {
        let rdata = res.data;
        let status = rdata.status;
        if (status) {
          this.popIndex = 3;
        } else {
          this.popIndex = 2;
        }
      });
    },
    reqCertificate() {
      let id = this.params.data.id;
      this.$http(API.CERTIFICATE, { id }, "get").then(res => {
        let rdata = res.data;
        if (rdata) {
          this.id = rdata.id;
          this.txid = rdata.txid;
          this.pageData = rdata.datas;
        }
      });
    },
    popRequest() {
      switch (this.popIndex) {
        case 1:
          this.reqChainCheck();
          break;
        case 4:
          this.reqCertificate();
          break;
        default:
          break;
      }
    }
  },
  mounted() {
    this.popRequest();
  }
};
</script>





