<template>
<div>
  <div class="oil-box-border index-table">
    <el-table :data="tableData">
      <el-table-column prop="fileName" label="文件名称"></el-table-column>
      <el-table-column prop="createTime" label="上传时间"></el-table-column>
      <el-table-column label="区块链存证ID" width="300">
        <template slot-scope="scope">
          <div class="cert-id" @click="jump2block(scope.row.txid)">{{ scope.row.txid }}</div>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="340">
        <template slot-scope="scope">
          <el-button size="mini" type="warning" @click="openNewPage(scope.row)">预览</el-button>
          <el-button size="mini" type="warning" @click="openNewPage(scope.row)">下载</el-button>
          <el-button size="mini" type="warning" @click="onView(scope.row)">查看证书</el-button>
          <el-button size="mini" type="warning" @click="onVerify(scope.row)">区块链验证</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
  <!-- <div class="page-box">
    <el-pagination 
      background 
      layout="prev,pager,next" 
      :pager-count="5" 
      :total="totalPage" 
      :current-page="currentPage">
    </el-pagination>
  </div> -->
  <popups v-if="popshow" :params="popParam" :close="popupClose"></popups>
</div>
</template>

<script>
import API from "@/api/api";
import { mapGetters } from "vuex";
import Popups from "@/common/popups";

export default {
  components: {
    Popups
  },
  data() {
    return {
      testData: [
        {
          fileName: "1212",
          createTime: "2018-10-09",
          txid:
            "546a8b7dd168ec741b7746b3d3f55defa4cf48d221e7c347cd49fbcca7853ace",
          id: "577b0a80-178d-4b77-af11-76dbe13d3c3f"
        }
      ],
      totalPage: 0,
      currentPage: 1,
      popshow: false,
      popParam: {}
    };
  },
  computed: {
    ...mapGetters(["tableData"])
  },
  props: {
    data: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  methods: {
    onView(row) {
      this.popshow = true;
      this.popParam = {
        index: 4,
        title: "区块链存证证书",
        data: row
      };
    },
    onVerify(row) {
      this.popshow = true;
      // console.log(row);
      this.popParam = {
        index: 1,
        title: "区块链校验",
        data: row
      };
    },
    onCheck(row) {
      this.popshow = true;
      this.popParam = {
        index: 0,
        title: "区块链存证ID"
      };
    },
    popupClose() {
      this.popshow = false;
    },
    jump2block(txid) {
      window.open(API.BLOCK_HOME + txid);
    },
    openNewPage(row) {
      let fp = row.filePath;
      if(fp) {
        window.open(API.HOST_STATIC + "/" + fp);
      }else{
        this.$message({
          type: "error",
          message: "没有文件链接"
        })
      }
    }
  }
};
</script>


