<template>
  <div v-title="'船代详情'">
    <data-table v-if="!isView" :data="tableData"/>
    <el-tabs class="mg-t-20" type="border-card" :value="tabName" @tab-click="handleTabClick">
      <!-- SOF -->
      <el-tab-pane label="SOF" name="sof">
        <ship-sof v-if="tabName==='sof'"/>
      </el-tab-pane>
      <!-- 提单 -->
      <el-tab-pane label="提单" name="bill">
        <ship-ord v-if="tabName==='bill'"/>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import DataTable from "@/common/dataTable";
import ShipOrd from "./ship_bill.vue";
import ShipSof from "./ship_sof.vue";

export default {
  components: {
    DataTable,
    ShipSof,
    ShipOrd
  },
  data() {
    return {
      tabName: "sof",
      tableData: []
    };
  },
  computed: {
    isView() {
      return this.$route.query.isView;
    }
  },
  methods: {
    handleTabClick(tab) {
      this.tabName = tab.name;
    }
  },
  mounted() {
    let tname = this.$route.query.page;
    tname && (this.tabName = tname);
  }
};
</script>





