<template>
  <div class="bill-box">
    <el-row>
      <el-col :span="12">
        <span class="oil-title-1">TANKER BILL OF LADING</span>
      </el-col>
      <el-col :span="2">
        <span class="is-required">B/L NO. : </span>
      </el-col>
      <el-col :span="10">
        <div class="oil-underline">{{ pageData.blNo }}</div>
      </el-col>
    </el-row>
    <div class="mg-t-20">
      <table class="table-border" style="width:100%">
        <tr>
          <td colspan="5" class="table-label">
            <span class="is-required">Shipped on board in apparent good order and condition by (Consignor)</span>
          </td>
          <td colspan="5">
            <span>{{ pageData.consignor }}</span>
          </td>
        </tr>
        <tr>
          <td colspan="2" class="table-label">
            <span class="is-required">on board the tanker</span>
          </td>
          <td colspan="3">
            <span>{{ pageData.vessel }}</span>
          </td>
          <td colspan="2" class="table-label">
            <span class="is-required">at the port of loading</span>
          </td>
          <td colspan="3">
            <span>{{ pageData.loadingPort }}</span>
          </td>
        </tr>
        <tr>
          <td colspan="2" class="table-label">
            <span class="is-required">Where of</span>
          </td>
          <td colspan="3">
            <span>{{ pageData.captain }}</span>
          </td>
          <td colspan="2" class="table-label">
            <span class="is-required">Is the Master, to be delivered to the port of discharge</span>
          </td>
          <td colspan="3">
            <span>{{ pageData.unloadingPort }}</span>
          </td>
        </tr>
        <tr>
          <td colspan="2" class="table-label">
            <span class="is-required">Consignee</span>
          </td>
          <td colspan="8">
            <span>{{ pageData.consignee }}</span>
          </td>
        </tr>
        <tr>
          <td colspan="2" class="table-label">
            <span class="is-required">Notify</span>
          </td>
          <td colspan="8">
            <span>{{ pageData.notify }}</span>
          </td>
        </tr>
        <tr>
          <td class="w_10" v-for="item in 10" :key="item">
            <span v-if="item===1">hidden rule</span>
          </td>
        </tr>
        <tr>
          <td colspan="5" rowspan="6">
            <p class="oil-text center oil-remark">{{ pageData.category }}</p>
          </td>
          <td colspan="5" class="table-label">
            <div class="oil-text bold center">QUANTITY</div>
          </td>
        </tr>
        <tr>
          <td colspan="3">
            <span>{{ pageData.metricTon }}</span>
          </td>
          <td colspan="2" class="table-label">
            <span class="is-required oil-bold">MT</span>
          </td>
        </tr>
        <tr>
          <td colspan="3">
            <span>{{ pageData.longTon }}</span>
          </td>
          <td colspan="2" class="table-label">
            <span class="is-required oil-bold">LT</span>
          </td>
        </tr>
        <tr>
          <td colspan="3">
            <span>{{ pageData.bbls }}</span>
          </td>
          <td colspan="2" class="table-label">
            <span class="is-required oil-bold">US BBLS 60F</span>
          </td>
        </tr>
        <tr>
          <td colspan="3">
            <span>{{ pageData.ltrs }}</span>
          </td>
          <td colspan="2" class="table-label">
            <span class="is-required oil-bold">LTRS AT 15C</span>
          </td>
        </tr>
        <tr>
          <td colspan="3">
            <span>{{ pageData.ltrsObsTrmp }}</span>
          </td>
          <td colspan="2" class="table-label">
            <span class="is-required oil-bold">LTRS AT OBS TRMP</span>
          </td>
        </tr>
        <tr>
          <td colspan="10">
            <span class="is-required oil-remark">{{ pageData.remark }}</span>
          </td>
        </tr>
        <tr>
          <td colspan="10">
            <p>The quantity, measurement, weight, gauge, quality nature and value and actual condition of the cargo unknown to the Vessel and the Master to be delivered at the port of discharge or so near thereto as the Vessel can safely get, always afloat upon prior payment of freight as agreed.</p>
          </td>
        </tr>
        <tr>
          <td colspan="5" class="table-label">
            <span class="is-required">This shipment is carried under and pursuant to the terms of the Charter dated</span>
          </td>
          <td colspan="5">
            <span class="oil-remark">{{ pageData.cpDate1 }}</span>
          </td>
        </tr>
        <tr>
          <td class="table-label">
            <span class="is-required">at</span>
          </td>
          <td colspan="4">
            <span>{{ pageData.cpDate2 }}</span>
          </td>
          <td class="table-label">
            <span class="is-required">Between</span>
          </td>
          <td colspan="4">
            <span>{{ pageData.cpDate3 }}</span>
          </td>
        </tr>
        <tr>
          <td colspan="1" class="table-label">
            <span class="is-required">and</span>
          </td>
          <td colspan="9">
            <span>{{ pageData.cpDate4 }}</span>
          </td>
        </tr>
        <tr>
          <td colspan="10">
            <p>as Charterer, and all the terms whatsoever of the said Charter except the rate and payment of freight specified therein apply to and govern the rights of the parties concerned in this shipment. Copy of the Charter may be obtained from the Shipper or Charterer.</p>
            <p>If this Bill of Lading is a document of title to which the Carriage of Goods by Sea Act of the United Sates, approved April 16, 1936, or similar legislation giving statutory effect to the International convention for the Unification of Certain Rules relating to Bills of Lading at Brussels of August 25, 1924, applies by reason of the port of loading or discharge being in territory in which the said Act or other similar legislation is in force, this Bill of Lading shall have effect subject to the provisions of the said Act or other similar legislation, as the case may be, which shall be deemed incorporated herein, and nothing herein contained shall be deemed a surrender by the carrier of any of its rights or immunities or an increase of any of its responsibilities or liabilities under said Act or other similar legislation. If any term of this Bill of Lading is repugnant to the said Act or other similar legislation as so incorporated, such terms shall be void to that extent but no further. The contract of carriage evidenced by this Bill of Lading is between the shipper, consignee and/or owner of the cargo and the owner or demise chartered of the vessel named herein to carry the cargo described above. It is understood and agreed that, other than said shipowner or demise charterer, no person, firm or corporation or other legal entity whatsoever, is or shall be deemed to be liable with respect to the shipment as carrier, bailee or otherwise in contract or in tort. If, however, it shall be adjudged that any other than said shipowner or demise charterer is carrier or bailee of said shipment or under any responsibility with respect thereto, all limitations of or exoneration’s from liability and all defenses provided by law or by the terms of the contract of carriage shall be available to such other.</p>
          </td>
        </tr>
        <tr>
          <td colspan="5" class="table-label">
            <span class="is-required">In witness where of, the Master has signed</span>
          </td>
          <td colspan="5">
            <span>{{ pageData.signNum }}</span>
          </td>
        </tr>
        <tr>
          <td colspan="10">
            <p>Bills of Lading of this tenor and date, one of which being accomplished, the others will be void.</p>
          </td>
        </tr>
        <tr>
          <td class="table-label">
            <span class="is-required">Dated at</span>
          </td>
          <td colspan="3">
            <span>{{ pageData.timeZone }}</span>
          </td>
          <td class="table-label">
            <span class="is-required">this</span>
          </td>
          <td>
            <span>{{ pageData.timeDay }}</span>
          </td>
          <td class="table-label">
            <span class="is-required">Day of</span>
          </td>
          <td>
            <span>{{ pageData.timeMonth }}</span>
          </td>
          <td class="table-label">
            <span class="is-required">,</span>
          </td>
          <td>
            <span>{{ pageData.timeYear }}</span>
          </td>
        </tr>
      </table>
    </div>
    <div class="mg-t-20">
      <el-row :gutter="20">
        <el-col :span="12">
          <span>&nbsp;</span>
        </el-col>
        <el-col :span="2">
          <span class="is-required">MASTER OF</span>
        </el-col>
        <el-col :span="10">
          <div class="oil-underline">
            <span>{{ pageData.captain }}</span>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import API from "@/api/api";
import { mapGetters } from "vuex";
import { fmtPageData } from "@/util/index";

export default {
  data() {
    return {
      pageData: {
        blNo: "",
        vessel: "",
        enterpriseNameEn: "",
        loadingPort: "",
        captain: "",
        unloadingPort: "",
        consignee: "",
        notify: "",
        category: "",
        metricTon: "",
        longTon: "",
        bbls: "",
        ltrs: "",
        ltrsObsTrmp: "",
        remark: "",
        cpDate1: "",
        cpDate2: "",
        cpDate3: "",
        cpDate4: "",
        signNum: "",
        timeZone: "",
        timeDay: "",
        timeMonth: "",
        timeYear: "",
        singedDate: "",
        masterName: ""
      }
    };
  },
  computed: {
    ...mapGetters(["deal_no", "blockData"]),
    isView() {
      return this.$route.query.isView;
    }
  },
  watch: {
    blockData(val) {}
  },
  methods: {
    reqPageData() {
      let oid = {
        deal_no: this.deal_no
      };
      this.$http(API.SHIP_BILL, oid, "get").then(res => {
        let odata = res.data;
        if (odata) {
          let pdata = fmtPageData(odata);
          this.pageData = pdata.pageData;
          // console.log(this.pageData);
          let tdata = pdata.attachmentList;
          this.$store.commit("setTableData", tdata);
        }
      });
    }
  },
  mounted() {
    // if (this.isView) {
    //   this.pageData = this.blockData;
    // } else {
      this.reqPageData();
    // }
  }
};
</script>



