<template>
  <div class="oil-card index-card">
    <div class="oil-card-head pad-0-20">
      <el-row class="oil-row">
        <el-col :span="8">
          <span> 卖家名称 :</span>
          <span>{{ orderData.seller_company }}</span>
        </el-col>
        <el-col :span="8">
          <span> 买家名称 :</span>
          <span>{{ orderData.buyer_company }}</span>
        </el-col>
      </el-row>
    </div>
    <div class="oil-card-body pad-0-20">
      <el-row class="oil-card-content oil-row">
        <el-col :span="8">
          <span>外贸合同号：</span>
          <span>{{ orderData.purchase_contract_no }}</span>
        </el-col>
        <el-col :span="8">
          <span>油种：</span>
          <span>{{ orderData.oil_type }}</span>
        </el-col>
        <el-col v-if="isUnload" :span="8">
          <span>卸货港：</span>
          <span>{{ unloadPort }}</span>
        </el-col>
        <el-col v-else :span="8">
          <span>装货港：</span>
          <span>{{ loadPort }}</span>
        </el-col>
        <el-col :span="8">
          <span>出口代理协议：</span>
          <span>{{ orderData.protocol }}</span>
        </el-col>
        <el-col :span="8">
          <span>数量：</span>
          <span>{{ number }}</span>
        </el-col>
        <el-col :span="8">
          <span>装货期：</span>
          <span>{{ loadDate }}</span>
        </el-col>
        <el-col :span="8">
          <span>船代：</span>
          <span>{{ orderData.shipagent }}</span>
        </el-col>
        <el-col :span="8">
          <span>货代：</span>
          <span>{{ orderData.freightagent }}</span>
        </el-col>
        <el-col :span="8">
          <span>商检：</span>
          <span>{{ orderData.inspector }}</span>
        </el-col>
        <!-- <el-col :span="8">
          <span>deal no：</span>
          <span>{{ orderData.deal_no}}</span>
        </el-col> -->
      </el-row>
      <el-row class="oil-card-foot oil-row">
        <!-- <el-col :span="8">
          <span>报关状态：</span>
          <span class="color-warning">{{ orderData.demurrage_rate }}</span>
        </el-col> -->
        <el-button class="float-r" type="warning" size="mini" @click="checkOrder(orderData)">查看详情</el-button>
      </el-row>
    </div>
  </div>
</template>

<script>
import { fmtDate, fmtTime } from "@/util/filter";

export default {
  data() {
    return {};
  },
  computed: {
    loadDate() {
      let sdate = this.orderData.laycan_start_date;
      let edate = this.orderData.laycan_end_date;
      return fmtDate(sdate) + " - " + fmtDate(edate);
    },
    number() {
      return this.orderData.num + this.orderData.quantity_unit_code;
    },
    loadPort() {
      return this.orderData.loading_country + " " + this.orderData.load_port;
    },
    unloadPort() {
      return this.orderData.discharge_country + " " + this.orderData.discharge_port;
    },
    isUnload() {
      return this.orderData.loading_country === "null";
    }
  },
  props: {
    orderData: {
      type: Object,
      default: {
        buyer_company: "",
        deal_no: "",
        deal_uuid: "",
        enterprise_id: null,
        enterprise_name: "",
        inspector: "",
        laycan_end_date: null,
        laycan_start_date: null,
        load_port: "",
        num: null,
        oil_type: "",
        protocol: "",
        purchase_contract_no: "",
        quantity_unit_code: "",
        seller_company: "",
        shipagent: ""
      }
    },
    checkOrder: {
      type: Function,
      default() {}
    }
  },
  methods: {}
};
</script>





