<script>
import API from "@/api/api";
import { mapGetters } from "vuex";
import { fmtPageData } from "@/util/index";
import DataTable from "@/common/dataTable";

export default {
  components: {
    DataTable
  },
  data() {
    return {
      pageData: {
        id: null,
        enterpriseId: null,
        status: null,
        unifiedcode: null,
        purchaseContractNo: null,
        inseastradeco: null,
        overseastradeco: null,
        enterprisenameCh: null,
        enterprisenameEn: null,
        checkoutno: null,
        customscode: null,
        declareCreditcode: null,
        declareCustomscode: null,
        declareCheckoutno: null,
        declareEnterprisename: null,
        productionCreditcode: null,
        productionCustomscode: null,
        productionCheckoutno: null,
        productionEnterprisename: null,
        customsinfoCustomscode: null,
        customsinfoIeportcode: null,
        customsinfoTrafmode: null,
        customsinfoVoyno: null,
        customsinfoDistinateport: null,
        customsinfoNativeshipname: null,
        customsinfoDeliveryno: null,
        customsinfoTrademode: null,
        customsinfoCutmode: null,
        customsinfoTransmode: null,
        customsinfoLicenseno: null,
        customsinfoTradecountry: null,
        customsinfoPackqty: null,
        customsinfoPacktype: null,
        customsinfoGrosswt: null,
        customsinfoNetwt: null,
        customsinfoEntyportcode: null,
        customsinfoEntrytype: null,
        customsinfoTradeareacode: null,
        customsinfoGoodsplace: null,
        customsinfoVsaorgcode: null,
        customsinfoOrgcode: null,
        customsinfoInsporgcode: null,
        customsinfoNote: null,
        goodsinfoDecgno: null,
        goodsinfoPriceperunit: null,
        goodsinfoDecltotal: null,
        goodsinfoGqty: null,
        goodsinfoGunit: null,
        goodsinfoCutmode: null,
        goodsinfoTradecurr: null,
        goodsinfoCodets: null,
        goodsinfoGname: null,
        goodsinfoQty1: null,
        goodsinfoQty2: null,
        goodsinfoDestinationcountry: null,
        goodsinfoOrigincountry: null,
        goodsinfoDistrictcode: null,
        contactperson: null,
        contactway: null,
        uuid: null,
        createtime: null,
        blockchainId: null
      },
      tableData: []
    };
  },
  computed: {
    ...mapGetters(["deal_no"]),
    isView() {
      return this.$route.query.isView;
    }
  },
  methods: {
    reqPageData() {
      let oid = {
        deal_no: this.deal_no
      };
      this.$http(API.BROKER, oid, "get").then(res => {
        let odata = res.data;
        if (odata) {
          let pdata = fmtPageData(odata);
          this.pageData = odata.pageData;

          let tdata = pdata.attachmentList;
          this.$store.commit("setTableData", tdata);
        }
      });
    }
  },
  mounted() {
    this.reqPageData();
  }
};
</script>



