<template>
  <div v-title="'首页'">
    <!-- <div class="bar-search">
      <order-search :form-data="form" :search="reqHomeData"></order-search>
    </div> -->
    <div>
      <home-item v-for="(order, i) in orderOptions" :key="i" :order-data="order" :checkOrder="jump2center"/>
      <div v-if="isNullTable" class="noInfo">无订单信息</div>
    </div>
    <div class="page-box">
      <!--分页-->
      <el-pagination 
        background 
        layout="prev,pager,next" 
        :pager-count="5" 
        :total="totalPage" 
        :current-page="currentPage" 
        @current-change="reqNewPage"></el-pagination>
    </div>
  </div>
</template>

<script>
import API from "@/api/api";
import HomeItem from "@/common/homeItem";
import OrderSearch from "@/common/searchOrder";

export default {
  components: { OrderSearch, HomeItem },
  data() {
    return {
      totalPage: 0,
      currentPage: 1,
      form: {
        customs_declaration_state: "",
        pageNum: 1,
        pageSize: 10
      },
      orderOptions: [
        // {
        //   buyer_company: "非主流",
        //   deal_no: "181024172626",
        //   deal_uuid: "5cf8e00a-43ca-4dec-8f37-e4dc304af5e4",
        //   enterprise_id: null,
        //   enterprise_name: "泉州石化-test",
        //   freightagent: "sgs",
        //   laycan_end_date: 1540915200000,
        //   laycan_start_date: 1540483200000,
        //   load_port: "",
        //   num: 789,
        //   oil_type: "柴油",
        //   protocol: "gasoil10ppm1024004",
        //   purchase_contract_no: "gasoil10ppm20181024004",
        //   quantity_unit_code: "MT",
        //   seller_company: "泉州石化-test"
        // }
      ]
    };
  },
  computed: {
    isNullTable() {
      return this.orderOptions.length < 1;
    }
  },
  watch: {
    isNullTable() {}
  },
  methods: {
    reqHomeData(data) {
      this.$http(API.HOME, this.form).then(res => {
        let rdata = res.data;
        if (rdata && rdata.datas) {
          this.orderOptions = rdata.datas;
          this.totalPage = rdata.total;
        }
      });
    },
    jump2center(order) {
      let deal_no = order.deal_no;

      this.$store.commit("setDealNo", deal_no);
      this.$store.commit("setOrderData", order);

      this.$router.push({
        name: "overview",
        query: { deal_no }
      });
    },
    reqNewPage(pageNum) {
      this.currentPage = pageNum;
      this.form.pageNum = pageNum;
      this.reqHomeData();
    }
  },
  mounted() {
    this.reqHomeData();
  }
};
</script>



