<template>
  <div id="app" class="oil">
    <div class="oil-header">
      <bar-top :unHome="unHome"></bar-top>
      <div class="banner-box">
        <div class="w-1180">
          <div class="banner-txt">海关创新监管平台</div>
          <bar-nav v-if="unHome" :routeName="routeName"></bar-nav>
        </div>
      </div>
    </div>
    <div class="oil-client">
      <router-view class="oil-order"></router-view>
    </div>
    <bar-foot></bar-foot>
  </div>
</template>

<script>
import BarTop from "@/common/barTop.vue";
import BarNav from "@/common/barNav.vue";
import BarFoot from "@/common/barFoot.vue";
import { reqCertificate } from "@/util/index";
import API from "@/api/api";

export default {
  components: { BarNav, BarTop, BarFoot },
  data() {
    return {};
  },
  computed: {
    unHome: {
      get() {
        return this.routeName !== "home";
      },
      set(val) {}
    },
    routeName() {
      return this.$route.name;
    },
    isView() {
      return this.$route.query.isView;
    },
    deal_no() {
      return this.$route.query.deal_no;
    }
  },
  watch: {
    routeName(val) {
      this.unHome = val !== "home";
    }
  },
  methods: {
    // 请求：登录
    reqLoginMsg() {
      this.$http(API.LOGIN, {}, "get").then(res => {
        let rdata = res.data;
        if (rdata) {
          this.loginSuccess();
        } else {
          location.href = API.LOGIN_PAGE;
        }
      });
    },
    // 请求：用户信息
    reqLoginUser() {
      this.$http(API.LOGIN_USER, {}, "get").then(res => {
        let rdata = res.data;
        if (rdata) {
          this.$store.commit("setUserData", rdata);
        }
      });
    },
    // 请求：当前订单信息
    reqOrderData(deal_no) {
      this.$http(API.HOME, { deal_no }).then(res => {
        let rdata = res.data;
        let datas = rdata.datas;
        if (datas && datas.length) {
          let odata = datas[0];
          this.$store.commit("setOrderData", odata);
          this.reqNodeData(odata);
        }
      });
    },
    loginSuccess() {
      this.reqLoginUser();
      this.setGlobalData();
    },
    setGlobalData() {
      let deal_no = this.deal_no;

      if (deal_no) {
        this.$store.commit("setDealNo", deal_no);
        this.reqOrderData(deal_no);
      }
    },
    // 请求：预览节点信息
    reqNodeData(item) {
      let isView = this.isView;
      if (isView) {
        let id = item.blockchain_id;
        reqCertificate(id, res => {
          this.$store.commit("setBlockData", data.datas);
        });
      }
    },
    reqCalendar() {
      this.$http("https://www.calendarindex.com/api/v1/holidays?", {
        country: "CN",
        year: "2018",
        api_key: "5c6b0328975ed56ad4acf175309bc6586ac0bbd8"
      }, "get").then(res => {
        // console.log(res.data);
      });
    }
  },
  created() {
    this.setGlobalData();
    this.reqLoginMsg();
  }
};
</script>



