<template>
  <div>
    <div class="w-1180 pad-20-0">
      <div class="img-box" style="width:360px;">
        <img src="/static/images/logo-custom.png" alt="" srcset="">
      </div>
    </div>
    <div class="bar-top">
      <div class="clearfix w-1180">
        <ul class="float-l" v-if="unHome">
          <li>
            <span class="oil-link-route" @click="jump2home">首页</span>
            <span class="el-icon-arrow-right"></span>
            <span>任务详情</span>
          </li>
        </ul>
        <ul class="float-r">
          <li>
            <a href="javascript:void(0)" style="color:#fff;margin-right:5px;">用户中心</a>
            <span>厦门海关</span>
            <span>|</span>
            <span class="pointer" @click="reqLogOut">退出</span>
          </li>
          <li>
            <i></i>
            <span>帮助</span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import API from "@/api/api";

export default {
  name: "tabBar",
  data() {
    return {
      checkindex: 0,
      systemName: "货代业务系统"
    };
  },
  props: {
    unHome: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    jump2home() {
      this.$router.push({
        name: "home"
      });
    },
    reqLogOut(){
      location.href = API.LOGOUT;
    }
  }
};
</script>





