<template>
  <!-- <el-menu :default-active="activeItem" mode="horizontal" router>
    <el-menu-item 
      :index="item.name" 
      :key="i" 
      v-for="(item, i) in menuList">
      <span slot="title">{{ item.title }}</span>
    </el-menu-item>
  </el-menu> -->
  <ul class="oil-nav">
    <li class="oil-nav-item" 
      v-for="(item, i) in menuList" 
      :key="i" 
      :class="item.name===routeName?'on':''"
      @click="jump2page(item)">
      <span>{{ item.title }}</span>
    </li>
  </ul>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "bar-nav",
  data() {
    return {
      menuList: [
        {
          title: "概况",
          name: "overview"
        },
        {
          title: "交易详情",
          name: "trade"
        },
        {
          title: "船代详情",
          name: "shipping"
        },
        {
          title: "报关行详情",
          name: "broker"
        },
        {
          title: "商检详情",
          name: "inspection"
        },
        {
          title: "收付汇详情",
          name: "payment"
        }
      ],
      companyName: "中化货代公司"
    };
  },
  computed: {
    ...mapGetters(["deal_no"])
  },
  props: {
    routeName: {
      type: String,
      default: "overview"
    }
  },
  watch: {
    routeName(val) {}
  },
  methods: {
    jump2page(item) {
      let deal_no = this.deal_no;
      let route = {
        name: item.name,
        query: { deal_no }
      }
      this.$router.push(route);
    }
  }
};
</script>



