<template>
  <div class="oil-step oil-row">
    <el-row class="oil-step-item" :gutter="20"  v-for="(item, i) in nodeList" :key="i">
      <el-col :span="4" class="text-right">
        <div class="oil-box-inline role-box">
          <div class="oil-portrait">
            <img :src="item.buyerLogo" alt="">
          </div>
          <!-- <div class="role-name">{{ item.buyerName }}</div> -->
        </div>
        <div class="oil-box-inline role-box">
          <div class="oil-portrait">
            <img :src="item.salerLogo" alt="">
          </div>
          <!-- <div class="role-name">{{ item.salerName }}</div> -->
        </div>
      </el-col>
      <el-col :span="1">
        <div class="icon-item">
          <div :class="iconName(i)"></div>
        </div>
      </el-col>
      <el-col :span="2">
        <div class="oil-title-2">{{ item.fm_time }}</div>
        <div>{{ item.fm_date }}</div>
      </el-col>
      <el-col :span="3" class="clearfix">
        <div class="float-l">
          <i class="el-icon-document"></i>
        </div>
        <div style="padding-left:15px;">
          <div>{{ item.appointEnterpriseName }}</div>
          <el-button v-if="item.pageName" type="text" size="mini" @click="viewNode(item)">查看</el-button>
          <el-button v-if="item.isView" type="text" size="mini" @click="popOpen(item)">预览</el-button>
        </div>
      </el-col>
      <el-col :span="8">
        <span class="el-icon-document">{{ item.description }}</span>
      </el-col>
      <el-col :span="4">
        <div @click="jump2block(item.blockchain_txid)" class="cert-id">
          <span class="el-icon-document"></span>
          <span>{{ item.blockchain_txid }}</span>
        </div>
      </el-col>
      <el-col :span="2">
        <span class="el-icon-document">已存证</span>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import API from "@/api/api";
import { mapGetters } from "vuex";
import { reqCertificate } from "@/util/index";
import { fmtDate, fmtTime } from "@/util/filter";

export default {
  data() {
    return {};
  },
  props: {
    tableData: {
      type: Array,
      default() {
        return [];
      }
    },
    popOpen: {
      type: Function,
      default() {}
    }
  },
  computed: {
    ...mapGetters(["eventCode"]),
    nodeList() {
      return this.fmtNodeData(this.tableData);
    }
  },
  watch: {},
  methods: {
    setEventCode(e) {
      let ecode = e.event_code;
      let entObj = this.eventCode[ecode];

      if (entObj) {
        e.appointEnterpriseName = entObj.eventName;
        e.pageName = entObj.pageName;
        e.isView = entObj.isView;
      }

      return e;
    },
    iconName(index) {
      let clazz = "icon-node";
      let len = this.tableData.length;
      if (index < 1) {
        return clazz + "-up";
      }
      if (index > len - 2) {
        return clazz + "-down";
      }
      return clazz;
    },
    viewNode(item) {
      // console.log(item);
      let id = item.blockchain_id;
      reqCertificate(id, data => {
        this.$store.commit("setBlockData", data.datas);
        let pname = item.pageName;
        this.jump2page(pname);
      });
    },
    fmtNodeData(data) {
      data.forEach(e => {
        // console.log(e.post_data);
        let pdata = JSON.parse(e.post_data);
        e.buyerName = pdata.buyerName;
        e.salerName = pdata.salerName;
        e.fm_date = fmtDate(e.create_date, "/", true);
        e.fm_time = fmtTime(e.create_date);
        e = this.setEventCode(e);
      });
      return data;
    },
    jump2block(txid) {
      window.open(API.BLOCK_HOME + txid);
    },
    jump2page(pageName) {
      let qname = pageName.split("-");
      let query = this.$route.query;

      query.isView = 1;
      query.page = qname[1];

      this.$router.push({
        name: qname[0],
        query
      });
    }
  }
};
</script>




