<template>
  <div v-title="'概况'">
    <div class="oil-card">
      <div class="oil-row">
        <span>外贸合同号：</span>
        <span>{{ orderData.purchase_contract_no }}</span>
        <!-- <el-tag size="mini">执行中</el-tag> -->
      </div>
    </div>
    <div class="oil-card">
      <el-row class="oil-row" :gutter="20">
        <el-col :span="4" class="text-right">
          <span>角色</span>
        </el-col>
        <el-col :span="1">
          <span></span>
        </el-col>
        <el-col :span="5">
          <span>事件记录</span>
        </el-col>
        <el-col :span="8">
          <span>重要信息提示</span>
        </el-col>
        <el-col :span="4">
          <span>区块链存证ID</span>
        </el-col>
        <el-col :span="2">
          <span>状态</span>
        </el-col>
      </el-row>
      <event-node :table-data="tableData" :popOpen="popOpen"></event-node>
    </div>
    <popups v-if="popshow" :params="popParam" :close="popupClose"></popups>
  </div>
</template>

<script>
import API from "@/api/api";
import { mapGetters } from "vuex";
import { fmtPageData } from "@/util/index";
import EventNode from "@/common/eventNode";
import Popups from "@/common/popups";

export default {
  components: {
    EventNode,
    Popups
  },
  data() {
    return {
      popshow: false,
      popParam: {},
      tableData: [
        // {
        //   prise: [""],
        //   id: 843,
        //   event_code: "4002",
        //   create_date: 1540289059000,
        //   blockchain_id: "02c2c3c4-ca3a-447e-a83e-8fecf04e3196",
        //   description: "委托船代",
        //   create_user: 100008253,
        //   deal_no: "181023175400",
        //   post_data:
        //     '{"createTime":"2018-10-12 15:02:20","dataCopyright":"SGS","eventCode":"1002","fileName":"PHP","filePath":"transaction/26eb2151607e4f949756bc2a1775ac7b.php","fileSize":"1219","fileSummary":"558028f9137439a319bb3cb16ee6bba9db34c025","fileType":"php"}',
        //   blockchain_txid:
        //     "44260ad84595186023f809149726dbfabf7ddf5a2273f9ca79d29d4c8b0ea2b2",
        //   busi_id: 80
        // }
      ]
    };
  },
  computed: {
    ...mapGetters(["deal_no", "orderData"])
  },
  watch: {
    orderData(val) {}
  },
  methods: {
    reqNodeList() {
      let oid = { deal_no: this.deal_no };
      this.$http(API.OVERVIEW, oid, "get").then(res => {
        let odata = res.data;
        if (odata && odata.content) {
          this.tableData = fmtPageData(odata.content);
        }
      });
    },
    popupClose() {
      this.popshow = false;
    },
    popOpen(row) {
      // this.popshow = true;
      // this.popParam = {
      //   index: 0,
      //   title: "区块链存证ID"
      // };

      let rdata = JSON.parse(row.post_data);
      let fp = rdata.filePath;
      if (fp) {
        window.open(API.HOST_STATIC + "/" + fp);
      } else {
        this.$message({
          type: "error",
          message: "没有文件链接"
        });
      }
    }
  },
  mounted() {
    this.reqNodeList();
  }
};
</script>





