<template>
  <div v-title="'商检详情'">
    <data-table v-if="!isView" :data="tableData"/>
    <div class="oil-title-1 mg-t-20">SGS商检机构</div>
    <div class="inspection mg-t-20">
      <div v-if="oil_type==='汽油'" class="oil-card">
        <div class="oil-card-info oil-row">
          <span>汽油品质</span>
        </div>
        <div class="oil-card-box oil-row">
          <el-row>
            <el-col :span="8">
              <span>密度：</span>
              <span>{{ pageData.gasolineDensity }}</span>
            </el-col>
            <el-col :span="8">
              <span>诱导期：</span>
              <span>{{ pageData.gasolineInductionPeriod }}</span>
            </el-col>
            <el-col :span="8">
              <span>终馏点：</span>
              <span>{{ pageData.gasolineBoilingPoint }}</span>
            </el-col>
            <el-col :span="8">
              <span>蒸气压：</span>
              <span>{{ pageData.gasolineVaporTension }}</span>
            </el-col>
            <el-col :span="8">
              <span>硫含量：</span>
              <span>{{ pageData.gasolineSulfurContent }}</span>
            </el-col>
            <el-col :span="8">
              <span>含氧量：</span>
              <span>{{ pageData.gasolineOxygenContent }}</span>
            </el-col>
            <el-col :span="8">
              <span>胶质：</span>
              <span>{{ pageData.gasolineGum }}</span>
            </el-col>
            <el-col :span="8">
              <span>馏程：</span>
              <span>{{ pageData.gasolineDistillation }}</span>
            </el-col>
            <el-col :span="8">
              <span>氧化安定性：</span>
              <span>{{ pageData.gasolineOxidationStability }}</span>
            </el-col>
            <el-col :span="8">
              <span>水溶性酸或碱：</span>
              <span>{{ pageData.gasolineAcidAlkali }}</span>
            </el-col>
            <el-col :span="8">
              <span>铜片腐蚀：</span>
              <span>{{ pageData.gasolineCopperCorrosion }}</span>
            </el-col>
            <el-col :span="8">
              <span>辛烷值（RON研究法）：</span>
              <span>{{ pageData.gasolineOctaneNumber }}</span>
            </el-col>
          </el-row>
        </div>
      </div>
      <div v-else class="oil-card">
        <div class="oil-card-info oil-row">
          <span>柴油品质</span>
        </div>
        <div class="oil-card-box oil-row">
          <el-row>
            <el-col :span="8">
              <span>密度：</span>
              <span>{{ pageData.dieseloilDensity }}</span>
            </el-col>
            <el-col :span="8">
              <span>闪点：</span>
              <span>{{ pageData.dieseloilFlashPoint }}</span>
            </el-col>
            <el-col :span="8">
              <span>十六烷值：</span>
              <span>{{ pageData.dieseloilCetaneValue }}</span>
            </el-col>
            <el-col :span="8">
              <span>灰分：</span>
              <span>{{ pageData.dieseloilAsh }}</span>
            </el-col>
            <el-col :span="8">
              <span>水分：</span>
              <span>{{ pageData.dieseloilWaterContent }}</span>
            </el-col>
            <el-col :span="8">
              <span>凝点：</span>
              <span>{{ pageData.dieseloilCondensationPoint }}</span>
            </el-col>
            <el-col :span="8">
              <span>酸度：</span>
              <span>{{ pageData.dieseloilAcidity }}</span>
            </el-col>
            <el-col :span="8">
              <span>闭口闪点：</span>
              <span>{{ pageData.dieseloilClosingFlash }}</span>
            </el-col>
            <el-col :span="8">
              <span>铜片腐蚀：</span>
              <span>{{ pageData.dieseloilCopperCorrosion }}</span>
            </el-col>
            <el-col :span="8">
              <span>运动粘度：</span>
              <span>{{ pageData.dieseloilViscosity }}</span>
            </el-col>
            <el-col :span="8">
              <span>硫含量：</span>
              <span>{{ pageData.dieseloilSulfurContent }}</span>
            </el-col>
          </el-row>
        </div>
      </div>
      <div class="oil-card">
        <div class="oil-card-info oil-row">
          <span>数量</span>
        </div>
        <div class="oil-card-box oil-row">
          <el-row>
            <el-col :span="8">
              <span>提单量：</span>
              <span>{{ pageData.billLadingTon }}</span>
            </el-col>
            <el-col :span="8">
              <span>船检量：</span>
              <span>{{ pageData.shipInspectionTon }}</span>
            </el-col>
            <el-col :span="8">
              <span>提单日：</span>
              <span>{{ pageData.billLadingDate }}</span>
            </el-col>
            <el-col>
              <span>备注 REMARK：</span>
              <span>{{ pageData.remark }}</span>
            </el-col>
          </el-row>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import API from "@/api/api";
import { mapGetters } from "vuex";
import { fmtPageData } from "@/util/index";
import DataTable from "@/common/dataTable";

export default {
  components: {
    DataTable
  },
  data() {
    return {
      tableData: [],
      pageData: {
        inspectorId: "",
        billLadingTon: null,
        billLadingBucket: null,
        billLadingDate: "",
        contactNumber: "",
        gasolineDensity: null,
        gasolineBoilingPoint: null,
        gasolineVaporTension: "",
        gasolineInductionPeriod: null,
        gasolineSulfurContent: null,
        gasolineOxygenContent: null,
        gasolineGum: null,
        gasolineDistillation: null,
        gasolineOxidationStability: null,
        gasolineAcidAlkali: null,
        gasolineCopperCorrosion: "",
        gasolineOctaneNumber: null,
        dieseloilDensity: null,
        dieseloilCetaneValue: null,
        dieseloilFlashPoint: null,
        dieseloilAsh: null,
        dieseloilCopperCorrosion: "",
        dieseloilClosingFlash: null,
        dieseloilAcidity: null,
        dieseloilCondensationPoint: null,
        dieseloilWaterContent: null,
        dieseloilViscosity: null,
        dealNo: "",
        linkman: "",
        remark: "",
        state: "",
        shipInspectionTon: null,
        shipInspectionBucket: null
      }
    };
  },
  computed: {
    ...mapGetters(["deal_no", "orderData", "blockData"]),
    isView() {
      return this.$route.query.isView;
    },
    oil_type() {
      return this.orderData.oil_type;
    }
  },
  watch: {
    orderData(val) {},
    blockData(val) {}
  },
  methods: {
    reqPageData() {
      let oid = {
        deal_no: this.deal_no
      };
      this.$http(API.INSPECTION, oid, "get").then(res => {
        let odata = res.data;
        if (odata) {
          let pdata = fmtPageData(odata);
          this.pageData = pdata.pageData;
          // console.log(this.pageData);
          let tdata = pdata.attachmentList;
          this.$store.commit("setTableData", tdata);
        }
      });
    }
  },
  mounted() {
    // if (this.isView) {
    //   this.pageData = this.blockData;
    // } else {
      this.reqPageData();
    // }
  }
};
</script>



