const protocol = location.protocol;
const urlStr = location.hostname;
const urlMain = 'customs.myoilbank.com';
const imgHosts = {
  test: 'http://image.test.mycrudeoil.com',
  uat: 'https://image9.mycrudeoil.com',
  prod: 'https://image.mycrudeoil.com'
};
const urlist = urlStr.split('.');

// 区分开发和生产环境
function checkEvn () {
  if (urlist.includes('localhost')) {
    return setUrl('test');
  }
  if (urlist.includes('test')) {
    return setUrl('test');
  }
  if (urlist.includes('uat')) {
    return setUrl('uat');
  }
  // envList.forEach(e => {
  //   if (urlist.includes(e)) {
  //     return setUrl(e);
  //   }
  // });
  return setUrl();
}
// 拼接API地址
function setUrl (env = '') {
  let str = env ? env + '.' + urlMain : urlMain;
  let net = [protocol, '//', str].join('');
  let img = imgHosts[env];
  return [net, img];
}
export default checkEvn();



