<template>
  <div v-title="'区块链存证证书'">
    <div class="cert-box">
      <div class="cert-pad">
        <div class="cert-body">
          <div class="text-center">
            <img src="/static/images/oilbank.png" alt="" srcset="">
          </div>
          <div class="text-center">
            <span style="font-size:28px;">区块链存证证书</span>
          </div>
          <!-- <div class="text-center">
            <span>证书编号：</span>
            <span>{{ id }}</span>
          </div> -->
          <div class="mg-t-20 color-info text-center">
            <i class="el-icon-date"></i>
            <span>区块链网络通用协调时间：{{ pageData.createTime }}</span>
          </div>
          <div class="mg-t-30 text-center">
            <div class="oil-title-2">区块链存证ID:</div>
            <div class="mg-t-10">{{ txid }}</div>
          </div>
          <div class="cert-list mg-t-30"></div>
          <el-row class="mg-t-30">
            <el-col :span="4">文件摘要值：</el-col>
            <el-col :span="20">{{ pageData.fileSummary }}</el-col>
          </el-row>
          <el-row class="mg-t-10">
            <el-col :span="4">文件名称：</el-col>
            <el-col :span="20">{{ pageData.fileName }}</el-col>
          </el-row>
          <el-row class="mg-t-10">
            <el-col :span="4">文件类型：</el-col>
            <el-col :span="20">{{ pageData.fileType }}</el-col>
          </el-row>
          <el-row class="mg-t-10">
            <el-col :span="4">大小：</el-col>
            <el-col :span="20">{{ pageData.fileSize }}</el-col>
          </el-row>
          <el-row class="mg-t-10">
            <el-col :span="4">数据主体：</el-col>
            <el-col :span="20">{{ pageData.dataCopyright }}</el-col>
          </el-row>
          <el-row class="mg-t-10">
            <el-col :span="4">存证时间：</el-col>
            <el-col :span="20">{{ pageData.createTime }}</el-col>
          </el-row>
          <el-row class="mg-t-30">
            <span class="font-12 color-info">声明：本证书通过壹能链网络存证，为平台数据做出全球通行的记录声明。</span>
          </el-row>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import API from "@/api/api";

export default {
  data() {
    return {
      pageData: {
        eventCode: "1002",
        fileName: "JS",
        createTime: "2018-10-12 14:43:31",
        fileSize: "18627",
        dataCopyright: "SGS",
        filePath: "transaction/94970011bd41426886101693b6c6205d.js",
        fileSummary: "4bc72243fc1da5e04222168528aa30fe1d93f17d",
        fileType: "js"
      },
      txid: "546a8b7dd168ec741b7746b3d3f55defa4cf48d221e7c347cd49fbcca7853ace",
      id: "577b0a80-178d-4b77-af11-76dbe13d3c3f"
    };
  },
  methods: {
    reqCertificate() {
      let id = this.$route.query.id;
      this.$http(API.CERTIFICATE, { id }, "get").then(res => {
        let rdata = res.data;
        if(rdata) {
          this.id = rdata.id;
          this.txid = rdata.txid;
          this.pageData = rdata.datas;
        }
      });
    }
  },
  mounted(){
    this.reqCertificate();
  }
};
</script>



