import API from '@/api/api';
import { http } from '@/api/http';
import { Message } from 'element-ui';
/**
 * @desc 统一处理页面数据
 * @author bobo
 * @argument data
 */
export function fmtPageData (data) {
  data = initTableData(data);
  data.pageData = initPageData(data);

  return data;
}

export function showMessage (data, msgType) {
  let status = data.status;
  let message = data.message;
  let type = status ? 'error' : 'success';
  let ifShow = msgType ? type === msgType : true;

  ifShow && message && Message({
    type,
    message
  });
}

function initPageData (data) {
  let datas = data.data;
  let pdata = {};
  if (datas) {
    pdata = JSON.parse(datas);
  }
  return pdata;
}

function initTableData (data) {
  let list = data.attachmentList;
  if (list && list.length) {
    list.forEach(e => {
      if (e.content) {
        let tdata = JSON.parse(e.content);
        parseJson(e, tdata);
      }
    });
  }
  return data;
}

function parseJson (obj, data) {
  for (let i in data) {
    obj[i] = data[i];
  }
}

export function reqCertificate (id, cb) {
  http(API.CERTIFICATE, { id }, 'get').then(res => {
    let rdata = res.data;
    if (rdata) {
      let odata = rdata.datas;
      cb(odata);
    }
  }).catch(res => {
    Message({
      type: 'error',
      message: '服务器繁忙，请稍后重试'
    });
  });
}



