export default {
  deal_no (state) {
    return state.deal_no;
  },
  testData (state) {
    return state.testData;
  },
  userData (state) {
    return state.userData;
  },
  eventCode (state) {
    return state.eventCode;
  },
  orderData (state) {
    return state.orderData;
  },
  tableData (state) {
    return state.tableData;
  },
  blockData (state) {
    return state.blockData;
  }
}



